"use client";
import { useState } from "react";
import "../../styles/footer.css";

export default function Footer() {
  const [subscribeForm, setSubscribeForm] = useState({ name: "", mobile: "" });
  const [errors, setErrors] = useState<any>({});

  const validateSubscribe = () => {
    const err: any = {};
    if (!subscribeForm.name.trim()) err.name = "Name is required";
    if (!/^[6-9]\d{9}$/.test(subscribeForm.mobile)) err.mobile = "Valid mobile required";
    setErrors(err);
    return Object.keys(err).length === 0;
  };

  const submitSubscribe = async () => {
    if (!validateSubscribe()) return;

    try {
      const res = await fetch("/api/sendEmail", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          formType: "subscribe",
          ...subscribeForm
        })
      });

      const data = await res.json();
      if (data.success) {
        alert("Subscribed successfully âœ…");
        setSubscribeForm({ name: "", mobile: "" });
      } else {
        alert("Failed to send subscription. Try again later.");
      }
    } catch (err) {
      console.error(err);
      alert("Something went wrong. Try again later.");
    }
  };

  return (
    <footer className="footer">
      <div className="footer-container">
        <div className="footer-col">
          <h4>SUBSCRIBE NOW</h4>
          <input type="text" placeholder="Name As Per Pancard" value={subscribeForm.name} onChange={e => setSubscribeForm({ ...subscribeForm, name: e.target.value })} />
          {errors.name && <span className="error">{errors.name}</span>}

          <input type="tel" placeholder="Your Mobile Number" value={subscribeForm.mobile} onChange={e => setSubscribeForm({ ...subscribeForm, mobile: e.target.value })} />
          {errors.mobile && <span className="error">{errors.mobile}</span>}

          <button className="send-btn" onClick={submitSubscribe}>Send Now</button>
        </div>
      </div>
    </footer>
  );
}
